<template>
  <div class="embed-responsive embed-responsive-1by1">
    <iframe
      @load="load"
      class="embed-responsive-item"
      src="https://drive.google.com/file/d/17lZJbD3F9ArHkZUHfDhFZqWw9SQO2cph/preview"
    ></iframe>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  created() {
    this.SET_TITLE("Guía de lenguaje claro y estilo");
  },
  mounted() {
    this.loader = this.$loading.show();
  },
  beforeRouteLeave(to, from, next) {
    // Oculta el loader antes de cambiar de pestaña
    this.loader.hide();
    next();
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    load() {
      console.log("Loaded...");
      this.loader.hide();
    }
  }
};
</script>

